:root {
  --background-monitor: #C9C7AF;
  --background-monitor-screen: #EEF1DB;
  --foreground: #485B63;
  --highlight: #f51111;
}

body {
  height: 100vh;
  overflow: hidden;
}

#root {
  height: calc(100% - 50px);
}

.monitor {
  background: var(--background-monitor);
  height: 100%;
  display: flex;
  padding: 25px 40px;
}

.monitor-screen {
  background: var(--background-monitor-screen);
  border-radius: 7px;
  box-shadow: inset 1px 1px 7px 7px rgba(0,0,0,0.2);
  text-shadow: 2px 2px 3px rgba(0,0,0,0.2);
  overflow: hidden;
  padding: 25px 5px;
  width: 100%
}
